import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanLoad, NavigationExtras, Router, Route } from '@angular/router';
import { Observable } from 'rxjs';
import { AuthService } from './auth.service';
import { CommonService } from '../../shared/services/common.service';
import { map, take } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate , CanLoad{

  constructor(public authService: AuthService,
     private commonService: CommonService,
     private router: Router) { }
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
      const url: string = state.url;
      console.log("Url is:", url)
      return this.checkLogin(url)
   
  }
  canLoad(route: Route): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> |boolean {
    const url = `/${route.path}`;

    if (!this.authService.isAuthenticated()) {
      this.commonService.navigate('');
      return false;
    }
    return this.authService.isLoggedIn         // {1}
    .pipe(
      take(1),                              // {2} 
        map((isLoggedIn: boolean) => {         // {3}
        console.log("isLoggedIn", isLoggedIn)
        const sessionId = 123456789;
        const navigationExtras: NavigationExtras = {
          queryParams: { session_id: sessionId },
          fragment: 'anchor'
        };
          if (!isLoggedIn) {
            this.commonService.navigate('');  // {4}
            return false;
          }
          return true;
        })
      )
  }


 
  checkLogin(url :string){
    if (!this.authService.isAuthenticated()) {
      this.commonService.navigate('');
      return false;
    }
    return this.authService.isLoggedIn         // {1}
    .pipe(
      take(1),                              // {2} 
        map((isLoggedIn: boolean) => {         // {3}
        console.log("isLoggedIn", isLoggedIn)
        const sessionId = 123456789;
        const navigationExtras: NavigationExtras = {
          queryParams: { session_id: sessionId },
          fragment: 'anchor'
        };
          if (!isLoggedIn) {
            this.commonService.navigate('');  // {4}
            return false;
          }
          return true;
        })
      )
  }

  
}
