import { Component, OnInit } from '@angular/core';
import { AuthService } from '@app/core/guards/auth.service';
import { SVGCollection } from '@app/shared/constants/svg';
import { CommonService } from '@app/shared/services/common.service';
declare let $:any;
@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {
  display:boolean = true;
  visibleSidebar1:boolean = false;
  svgs = SVGCollection;
  constructor(private authService : AuthService,
    private commonService : CommonService) { }

  ngOnInit(): void {
    
    // this.sidebarScript()
  }


  openNav() {
    document.getElementById("mySidenav")!.style.width = "250px";
    document.getElementById("main")!.style.marginLeft = "250px";
    // document.body.style.backgroundColor = "rgba(0,0,0,0.4)";
    document.body.style.backgroundColor = "";

  }

  /* Set the width of the side navigation to 0 and the left margin of the page content to 0, and the background color of body to white */
  closeNav() {
    document.getElementById("mySidenav")!.style.width = "0";
    document.getElementById("main")!.style.marginLeft = "40px";
    document.body.style.backgroundColor = "";
  }


  sidebarScript(){
  console.log("Sidebar clicked")
  $(document).ready(function () {
    $("#sidebar").mCustomScrollbar({
        theme: "minimal"
    });

    $('#dismiss, .overlay').on('click', function () {
        $('#sidebar').removeClass('active');
        $('.overlay').removeClass('active');
    });

    $('#sidebarCollapse').on('click', function () {
        $('#sidebar').addClass('active');
        $('.overlay').addClass('active');
        $('.collapse.in').toggleClass('in');
        $('a[aria-expanded=true]').attr('aria-expanded', 'false');
    });
});
}

logout(){
  let body = {
      "access_token": this.commonService.getSessionStorage('token', false)
  }
  this.authService.logout(body).subscribe((res:any)=>{
    if(res.status == 200){
      this.authService.loggedIn.next(false);
      this.commonService.clearLocalStorage();
      this.commonService.clearSessionStorage();
      this.commonService.navigate('')
    }
    
  })
}
}
