import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Observable } from 'rxjs/internal/Observable';
import { Subject } from 'rxjs/internal/Subject';

@Injectable({
  providedIn: 'root'
})
export class BroadcastService {

  constructor() { }


  private loaderSubject = new Subject<any>();
  private toastSubject = new Subject<any>();
  private dialogSubject = new Subject<any>();

  handleDialog(dialogObj: any) {
    this.dialogSubject.next(dialogObj);
  }

  getDialog(): Observable<any> {
    return this.dialogSubject.asObservable();
  }

  handleLoader(loader: any) {
    this.loaderSubject.next({ state: loader });
  }

  getLoader(): Observable<any> {
    return this.loaderSubject.asObservable();
  }

  handleToast(toastObj: any) {
    this.toastSubject.next(toastObj);
  }

  getToast(): Observable<any> {
    return this.toastSubject.asObservable();
  }

  ConfirmedValidator(controlName: string, matchingControlName: string) {
    return (formGroup: FormGroup) => {
      const control = formGroup.controls[controlName];
      const matchingControl = formGroup.controls[matchingControlName];
      if (matchingControl.errors && !matchingControl.errors.confirmedValidator) {
        return;
      }
      if (control.value !== matchingControl.value) {
        matchingControl.setErrors({ confirmedValidator: true });
      } else {
        matchingControl.setErrors(null);
      }
    }
  }
}
